import React from "react";
import { Input, Modal , Select } from "antd";
import { SaveFilled } from "@ant-design/icons";
import IntlMessages from "util/IntlMessages";
import { FormattedMessage } from "react-intl";

const Option = Select.Option;

class AddUser extends React.Component {
  constructor() {
    super()
    this.state = {
      name :'',
      mobile :'',
      password :'',
    }
  }

  render() {
    const { onAddUser, onToggleModal, open } = this.props;
    const { name , mobile ,gender , password } = this.state;
    return (
      <Modal
        okText={<SaveFilled />}
        cancelText={ <IntlMessages id="modal.cancelSave"/> }
        title={<IntlMessages id="student.addStudent"/>}
        toggle={onToggleModal} visible={open}
        closable={false}
        onOk={() => {
          if (name === '' || mobile === '' || gender === '' || password === '')
            return;
          onToggleModal("addUserState");
          onAddUser({ name , mobile , gender , password });
          this.setState({ name: '' , mobile : '' ,  gender : '' , password : '' })
        }}
        onCancel={()=> {
          onToggleModal("addUserState")
          this.setState({ name: '' , mobile : '' ,  gender : '' , password : '' })
        }}>

        <div  className="gx-modal-box-row">
          <div className="gx-modal-box-form-item">
            <div className="gx-form-group">
              <FormattedMessage id="columns.name" defaultMessage="name">
              {
                placeholder => (
                  <Input
                  required
                  value={name}
                  placeholder={placeholder}
                  onChange={(event) => this.setState({name: event.target.value})}
                  margin="none"/>
                  )
                }
            </FormattedMessage>
            </div>
            <div className="gx-form-group">
              <FormattedMessage id="columns.mobile" defaultMessage="name">
              {
                placeholder => (
                  <Input
                  required
                  value={mobile}
                  placeholder={placeholder}
                  onChange={(event) => this.setState({mobile: event.target.value})}
                  margin="none"/>
                  )
                }
            </FormattedMessage>
            </div>

            <div className="gx-form-group">
              <FormattedMessage id="columns.password" defaultMessage="name">
              {
                placeholder => (
                  <Input
                  required
                  value={password}
                  placeholder={placeholder}
                  onChange={(event) => this.setState({mobile: event.target.value})}
                  margin="none"/>
                  )
                }
            </FormattedMessage>
            </div>
            <div className="gx-form-group">
              <FormattedMessage id="columns.gender" defaultMessage="name">
              {
                placeholder => (
                  <Select onChange={(value) => this.setState({gender: value})} placeholder={placeholder} className="gx-mr-3 gx-mb-3" defaultValue={gender} style={{width: 300}} >
                    <Option value=""></Option>
                    <Option value="female"><IntlMessages id="option.female"/></Option>
                    <Option value="male"><IntlMessages id="option.male"/></Option>
                  </Select>
                  )
                }
            </FormattedMessage>
            </div>
          </div>
        </div>
      </Modal>
    );
  }
}

export default AddUser;
