import React , { useState , useEffect } from "react";
import { Input, Modal , Select } from "antd";
import { SaveFilled } from "@ant-design/icons";
import IntlMessages from "util/IntlMessages";
import { FormattedMessage } from "react-intl";

const Option = Select.Option;

const EditUser = ({ onSaveUser, onToggleModal, open, user }) => {
  
    const [name, setName] = useState('')
    const [mobile, setMobile] = useState('')
    const [password, setPassword] = useState('')
    const [gender, setGender] = useState('')

    useEffect(() => {
      if(Object.keys(user).length !== 0){
        setName(user.name)
        setMobile(user.mobile)
      }
    }, [user])

    return (
      <Modal
        okText={<SaveFilled />}
        cancelText={ <IntlMessages id="modal.cancelSave"/> }
        title={<IntlMessages id="student.saveStudent"/>}
        toggle={onToggleModal} 
        visible={open}
        closable={false}
        onOk={() => {
          if (name === '' || mobile=== '' || gender==='')
            return;
          onToggleModal("editUserState");
          onSaveUser({ id:user.id , name , mobile });
          setName(name)
          setMobile(mobile)
        }}
        onCancel={()=> {
          onToggleModal('editUserState')
          setName(user.name)
          setMobile(user.mobile)
        }}>

        <div  className="gx-modal-box-row">
          <div className="gx-modal-box-form-item">
            <div className="gx-form-group">
              <FormattedMessage id="columns.name" defaultMessage="name">
              {
                placeholder => (
                  <Input
                  required
                  value={name}
                  placeholder={placeholder}
                  onChange={(event) => setName(event.target.value)}
                  margin="none"/>
                  )
                }
            </FormattedMessage>
            </div>
            <div className="gx-form-group">
              <FormattedMessage id="columns.mobile" defaultMessage="name">
              {
                placeholder => (
                  <Input
                  required
                  value={mobile}
                  placeholder={placeholder}
                  onChange={(event) => setMobile(event.target.value)}
                  margin="none"/>
                  )
                }
            </FormattedMessage>
            </div>

            <div className="gx-form-group">
              <FormattedMessage id="columns.password" defaultMessage="name">
              {
                placeholder => (
                  <Input
                  required
                  value={password}
                  placeholder={placeholder}
                  onChange={(event) => setPassword(event.target.value)}
                  margin="none"/>
                  )
                }
            </FormattedMessage>
            </div>
            <div className="gx-form-group">
              <FormattedMessage id="columns.gender" defaultMessage="name">
              {
                placeholder => (
                  <Select onChange={(value) => setGender(value)} placeholder={placeholder} className="gx-mr-3 gx-mb-3" defaultValue={gender} style={{width: 300}} >
                    <Option value=""></Option>
                    <Option value="female"><IntlMessages id="option.female"/></Option>
                    <Option value="male"><IntlMessages id="option.male"/></Option>
                  </Select>
                  )
                }
            </FormattedMessage>
            </div>
          </div>
        </div>
      </Modal>
    );
}

export default React.memo(EditUser);
