import React from "react";
import {Button, Card, Table , Row , Col , Divider , Popconfirm, message} from "antd";
import IntlMessages from "util/IntlMessages";
import { PlusCircleFilled, DeleteOutlined , EditOutlined , EyeFilled , CheckCircleTwoTone , ExclamationCircleTwoTone} from "@ant-design/icons";
import { fetchUsers , CreateUser , UpdateUser, DeleteUsers } from "../../../appRedux/actions/Users";
import { connect } from "react-redux";
import AddUser from "components/users/AddUser";
import EditUser from "components/users/EditUser";

class Members extends React.Component {

  state = {
    selectedRowKeys: [], // Check here to configure the CRM column
    loading: false,
    visible: false,
    user:{}
  };
 
  onSelectChange = (selectedRowKeys) => {
    console.log('selectedRowKeys changed: ', selectedRowKeys);
    this.setState({ selectedRowKeys });
  };

  confirm = (ids) => {
    this.setState({ loading: true });
    this.props.DeleteUsers({ids})
    .then( async ()=>{
      await this.props.fetchUsers()
      message.success('تم حذف المدرس');
      this.setState({ loading: false , selectedRowKeys:[]});
    }).catch(()=>{
      message.error('عطل أثناء العملية');
      this.setState({ loading: false , selectedRowKeys:[]});
    });   
  }

  handleVisibleChange = (visible) => {
    const { selectedRowKeys  } = this.state;
  
    if (selectedRowKeys.length > 0) {
      this.setState({visible}); 
    }
  };
  cancel = (e) => {
    this.setState({ selectedRowKeys:[]  });
  } 
  
  componentDidMount(){
    this.props.fetchUsers()
  }

  render() {
    const {loading , selectedRowKeys , visible , user} = this.state;
    const rowSelection = {
      selectedRowKeys,
      onChange: this.onSelectChange,
    };
    const hasSelected = selectedRowKeys.length > 0;

    const columns = [{
      title: <IntlMessages id="columns.name"/>,
      dataIndex: 'username',
      align:'right'
    }, {
      title: <IntlMessages id="columns.mobile"/>,
      dataIndex: 'mobile',
      align:'right'
    },{
      title: <IntlMessages id="columns.status"/>,  dataIndex: '', key: 'x', render: ({status}) => (
        <span>
          {status === 'Active' ? <CheckCircleTwoTone twoToneColor="#40a9ff"  /> :  <ExclamationCircleTwoTone twoToneColor="#eb2f96" /> }
        </span>
      ),
     align:'right'
    },{
      title: <IntlMessages id="columns.operation"/>, dataIndex: '', key: 'x', render: ({id}) => (
        <span>
          <Button type="primary" icon={<EditOutlined />}> تعديل </Button>
          <Divider type="vertical"/>
          <Button type="primary" icon={<EyeFilled />} > تفاصيل </Button>
        </span>
      ),
     align:'right'
    }];

    return (
      <Row>
       <Col span={24}>
        <Card title={<h1><IntlMessages id="sidebar.Users"/></h1>}>
            <Button size="large" icon={<PlusCircleFilled />}  type="primary" />
            <Divider type="vertical"/>
            <Popconfirm visible={visible} onVisibleChange={this.handleVisibleChange} title="هل أنت متأكد ؟" onConfirm={ () => this.confirm(selectedRowKeys) } onCancel={this.cancel} okText="نعم" cancelText="لا">
              <Button  size="large" icon={<DeleteOutlined/>} disabled={!hasSelected} type="danger" loading={loading} />
            </Popconfirm>
          <div className="gx-mb-3">
            <span style={{marginLeft: 8}}>
              {hasSelected ? `تم تحديد ${selectedRowKeys.length} عناصر` : ''}
            </span>
          </div>
          <AddUser open={this.state.addStudentState} onAddStudent={this.onAddStudent} onToggleModal={this.onToggleModal} />
          <EditUser open={this.state.editStudentState} user={user} onSaveStudent={this.onSaveStudent} onToggleModal={this.onToggleModal} />
          <Table rowKey={record => record.id} direction="rtl" bordered={true} className="gx-table-responsive" rowSelection={rowSelection} columns={columns} dataSource={this.props.users}/>
        </Card>
      </Col>
    </Row>
    );
  }
}

function mapStateToProps(state) {
  return {
    users: state.users,
  };
}
export default connect(mapStateToProps,{ fetchUsers , CreateUser , UpdateUser, DeleteUsers }  )(Members)


